@media screen and (max-width:500px) {
    .subscrible-area{
        flex-direction: column;
     }

    .subscrible{
        width: 100%;
        height: 20rem;
        margin: 2rem 0;
    }

    .subscrible-subtitle{
        width: 22rem;
        margin-top: -4rem;
        margin-bottom: -4rem;
        font-size: 1rem;
    }

    .subscrible-input-area{
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        flex-direction: column;
        align-items: center;
    }

    .subscrible-input{
        width: 20rem;
        height: 3rem;
        border-radius: 3px;

    }

    .subscrible-button{
        width: 11rem;
        height: 3rem;
        border-radius: 3px;
    }
}