@media screen and (max-width:500px) {
    
.related-products{
    margin-top: 2rem;    
    overflow-x: hidden;
    height: 22rem;
}
.related-products-title{
    font-size: 1.5rem;
}

.related-area{
    width: 100%;
    height: 3rem;
    margin-left: -1rem;
}

.left-right-arrows .bt-roll{
    width: 3rem;
    height: 2.5rem;
    gap: 0;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding-bottom: 0.5rem;
}

.related-products-area{
    gap: 0rem;
}


}