.shopp-checkout{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 60rem;
    position: relative;
}

.breadcrumbsCO{
    align-self: flex-start;
    margin-left: 4rem;
    font-weight: bolder;
    margin-bottom: 2rem;

}

.breadcrumbsCO .breadc:hover {
    color: var(--theme-color-green);
    cursor: pointer;
}
.formulary-area{
    width: 80%;
    height: 40rem;
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

.legend-formulary{
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 1rem;
    margin-left: 0.6rem;
}

.formulary{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: max-content;
    font-size: 0.8rem;
}

.formulary fieldset{
    border: none;
    display: flex;
    flex-direction: column;
    height: 40rem;
    gap: 0.8rem;
}

.input-formulary{
    border: 1px solid #7c7c7c;
    width: 100%;
    height: 2rem;
    margin: 0.5rem 0rem 0.5rem;
}

.input-area-div{
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
}
.double-input-div{
    display: flex;
    flex-direction: column;
}
.input-area-div .input-formulary{
    width: 20rem;
}





.details-you-order{
    height: 45rem;
    width: 35rem;
    border: 1px solid #7c7c7c;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
    justify-content: space-around;
    margin-top: 0.3rem;
}
 
.title-you-order{
    align-self: flex-start;
    margin-left: 1rem;
}

.product-resume{
    width: 100%;
    height: max-content;
    margin-bottom: 2rem;
    margin-left: 1rem;
}

.table-resume{
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;

}

.tr-subtotal{
    margin-top: 4rem;
    
}

.table-resume tr{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 1rem;
    margin-bottom: 1rem;
}

.tr-thead{
    padding-bottom: 0.8rem;
    border-bottom: 1px solid #7c7c7c;
}

.tr-total{
    padding-top: 0.8rem;
    border-top: 1px solid #7c7c7c;
    font-weight: bold;
}

.paypal-check{
    border-top: 1px solid #7c7c7c;
    display: flex;
    flex-direction: column;
    align-items: left;
    font-size: 0.8rem;
    padding-top: 1rem;
    margin-left: 1rem;
}

.paypal-check p{
    margin-left: 1rem;
}

.checkbox-div{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
}

.buttons-formulary-area{
    width: 100%;
    height: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    margin-top: 1.5rem;

}

.bt-create-acount{
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.bt-place-order{
    position: absolute;
    width: 8rem;
    height: 2rem;

    right: 9rem;
    top: 50rem;


    color: #fff;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border: none;
    font-size: 1rem;
}

.list-itens-resume{
    width: 95%;
    height: 13rem;
    overflow-y: scroll;
}

.checkbox-div input:hover{
    cursor: pointer;
}




.submit-message{
    width: 80%;
    margin : auto;
    bottom: 4rem;
    position: fixed;
    height: 80vh;
    border: none;
    background-image: linear-gradient(to right, #009a50 60%, #b6b6b6 100%);
    color: #fff;
    font-size: 2rem;
    z-index: 999;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}   

.submit-message span{
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
}

.submit-message .area-buttons{
    width: 30rem;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: -4rem;
}

.submit-message button{
    border: none;
    height: 2.5rem;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 0.2rem;
    cursor: pointer;
    
}

.submit-message button:hover{
    scale: calc(1.1);
    background-color: #d90b49;
    color: #fff;

}
