@media screen and (max-width:500px) {

    .sidebarMenu{
        height: 102vh;
    }
    .close-sidebar-icon{
        font-size: 1rem;
    }

    .navigation-area{
        height: 40rem;
    }

    .loginArea-hr{
        width: 90%;
        border-top : 2px solid #4a4a4a;
    }

    .buttons-area-login{
        width:9rem;
        height: 3rem;
        align-items: center;
    }
    .buttons-area-login .bt{
        font-size: 1.5rem;
    }

    .bt-login-hr{
        height: 3rem;
        margin: 0rem 1rem;
    }

    .options-area{
        margin-bottom: 14rem;
        gap: 4rem;
    }

    .options-area .bt{
        font-size: 1.2rem;
    }
}