@media screen and (max-width:500px) {

    .add-review{
        font-size: 1.2rem;
    }

    .add-review-input-area{
        flex-direction: column;
        gap: 1rem;
    }

    .add-review-input-area .inp{
        width: 100%;
        height: 2.5rem;
        font-size: 1.2rem;
    }

    .add-rating-review-area{
        gap: 1rem;
        flex-direction: column;
    }


    .add-rating-area{
        display: flex;
        flex-direction: column;
        gap: 0;
    }

    .rating-ul{
        width: 140%;
        display: flex;
        flex-direction: row;
        margin-top: 0.5rem;
        font-size: 1.1rem;
        list-style: none;
        margin-left: -2rem;
    }

    .add-review-text{
        height: 10rem;
        border: 1px solid var(--theme-color-gray);
    }

    .add-review-submit{
        width: 5rem;
        height: 2rem;
        border: 1px solid var(--theme-color-gray);
        border-radius: 3px;
        transition: 0.2s;
    }
    .add-review-submit:hover{
        cursor: pointer;
        background-color: var(--theme-color-green);
        scale: calc(1.1);
    }

}