@media screen and (max-width:500px){

    .products-name{
        align-items: center;
      }
      
      .breadcrumbsPn{
          margin-left: 0;
          font-size: 1.2rem;
      }
      
      .product{
          align-items: center;
          height: max-content;
          flex-direction: column;
      }
      
      .product-logo-area{
          width: 100%;
          height: max-content;
      }
      
      .product-main-img{
          height: 22rem;
      }
      
      .bt-rollers{
          display: none;
      }
      
      .img-conteiner-items{
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
      }
      
      
      
      .product-info-area{
          margin-left: 3rem;
          height: 100%;
      }
      
      .price-rate{
          width: 13rem;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
      }
      .price-rate .price{
          font-size: 1.5rem;
          color: var(--theme-color-green);
      }
      
      .product-decription{
          font-size: 0.8rem;
      }
      
      .selection-type-product{
          width: 33rem;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
      }
      
      .select-size,.select-color-area{
          border: none;
          width: 14rem;
          height: 2.5rem;
          align-items: center;
          display: flex;
          flex-direction: row;
          padding: 0rem 1rem 0rem 1rem;
          font-size: 0.9rem;
          gap: 1rem;
      }
      
      .size-bt{
          width: 2rem;
          height: 2rem;
          border: none;
      }
      .size-bt:hover{
          cursor: pointer;
          background-color: var(--theme-color-green);
      }
      
      .select-color-area{
          justify-content: space-between;
      }
      .select-color{
          border: none;
          width: 2rem;
          height: 2rem;
      }
      .select-color:hover, .select-size:hover{
          cursor: pointer;
      }
      
      .product-quanties{
          display: flex;
          flex-direction: row;
          width: 10rem;
          height: 2.5rem;
          align-items: center;
          justify-content: space-between;
      }
      
      .bt-area{
          margin-top: 1rem;
          display: flex;
          flex-direction: row;
          width: 18rem;
          height: 2.5rem;
          justify-content: space-between;
          align-items: center;
      }
      .bt-area .bt-product{
          border: none;
          background-color: #fff;
          align-items: center;
          text-align: center;
          width: 2rem;
          height: 2.5rem;
      }
      .bt-area .bt-product:hover{
          cursor: pointer;
          scale: calc(1.1);
      }
      .bt-product.add-to-cart{
          width: 10rem;
          margin-top: 1rem;
          background-color: var(--theme-color-green);
          color: #fff;
      }
      .bt-area-bt{
          border: none;
      }
      .bt-area-bt:hover{
          cursor: pointer;
          scale: calc(1.2);
      }
      
      
      
      .product-info-area{
          margin-left: 0rem;
          height: max-content;
          
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-around;
      }
      
      .product-title{
            font-size: 2.5rem;
      }
      
      .price-rate .price{
          font-size: 2rem;
      }
      
      .selection-type-product{
          margin-top: 1rem;
          width: max-content;
          justify-content: space-around;
          flex-direction: column;
          gap: 1rem;
          align-items: center;
          justify-content: center;
      }
      
      .select-size,.select-color-area{
          width: 10rem;
          height: 3rem;
          font-size: 1rem;
          gap: 1rem;
          justify-content: center;
      }
      
      .size-bt{
          width:4rem;
          height:3rem;
          border-radius: 2px;
          font-size: 1.2rem;
          font-weight: bold;
      }
      
      .select-color{
          border: none;
          width: 2rem;
          height: 2rem;
      }
      
      .select-color:hover, .select-size:hover{
          cursor: pointer;
      }
      
      .product-quanties{
          height: 3rem;
      }
      .product-quanties .bt{
          align-items: center;
          justify-content: center;
          text-align: center;
          font-size: 2rem;
          height: 3rem;
          width: 3rem;
          border-radius: 1px;
      }
      
      .quantitie-number{
          font-size: 1.5rem;
      }
      
      .bt-area{
          margin-top: 1.5rem;
          flex-direction: column;
          height: max-content;
          justify-content: space-around;
      }
      
      .bt-area a .bt-product{
          border: 3px;
          width: 12rem;
          height: 3.5rem;
          font-size: 1.2rem;
          font-weight: bold;
      }
      
      .bt-area-share-fav{
          width: 9rem;
          height: 2rem;
          display: flex;
          flex-direction: row;
          justify-content: space-around;
      }
      .bt-area-share-fav .bt-area-bt{
          background: transparent;
      }
      .bt-area-share-fav svg{
        width: 4rem;
        height: 2rem;
      }
      
      

      .description{
        margin-top: 3rem;
        align-items: center;
      }
      
      
      .desc-title-area .bt{
          height: 3.5rem;
          font-size: 1.2rem;
          font-weight: bold;

      }
}

