
.related-products{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 30rem;
    margin-bottom: 4rem;
}

.related-area{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0rem 1rem 1rem;
    align-items: center;
}

.left-right-arrows{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.left-right-arrows .bt-roll{
    border: none;
    width: 2rem;
    height: 1.5rem;
    background-color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
}

.left-right-arrows .bt-roll:hover{
    cursor: pointer;
    color: #fff;
    background-color: var(--theme-color-green);
}

.related-products-area{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 80%;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    transition: all ease 0.6s;
}
