@import '../../../styles/colors.css';

@media screen and (max-width: 500px){


.top-menu{
    /* display: none; */
    height: 6rem;
    flex-direction: column;
    justify-content: space-around;
}

.left-area-links{
    width: 100%;
    height: 3rem;
    font-size: 1rem;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: space-around;
    padding-left: 1rem 0 1rem;
    box-sizing: border-box;
    margin-left: 0;
}

.telphone{
    padding-left: 1rem;
    box-sizing: border-box;

}

.email{
    padding-right: 1rem;
    box-sizing: border-box;
}

.social-icons{
    right: 1rem;
    width: 10rem;
    height: 2.5rem;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    padding-bottom: 1rem;
}

.social-icon {
    width: 1.6rem;
    height: 1.6rem;
}

.main-nav{
    flex-direction: column;
    height: 10rem;
    gap: 0;
}

.nav-logo{
    font-size: 1.9rem;
}






.nav-right{
    margin-top: -3rem;
    padding-top: 1rem;
    width: 100%;
    height: 4rem;
    background-color: #fff;
    justify-content: center;
    gap: 3rem;
    margin-right: 0;
}

.navbarIcon{
    width:1.8rem;
    height:1.8rem;
}

.nav-pages{
    width: 100%;
    height: 4rem;
    text-align: center;
    padding-right: 2rem;
    font-size: 1.2rem;
    
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr) ;
    row-gap: 0;
    grid-template-areas: 
    "a-home-page a-women-page a-men-page "
    "a-kids-page a-jewlerry-page a-accesories-page"
    ;
    display: none;

}

.items-On-Cart{
    left: 7.5rem;
    top: 13rem;
}

}