@import "../../styles/colors.css";

.products-name{
    width: 80%;
    height: max-content;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.breadcrumbsPn{
    margin-left: 3rem;
    font-weight: bolder;
}

.breadcrumbsPn .breadc:hover {
    color: var(--theme-color-green);
    cursor: pointer;
}


.product{
    width: 100%;
    height: 35rem;
    display: flex;
    flex-direction: row;
}

.product-logo-area{
    width: 50%;
    height: max-content;
    display: flex;
    flex-direction: column;
}

.product-main-img{
    background-color: #000;
    width: 100%;
    height: 25rem;
}

.roller-imgs-area{
    width: 100%;
    height: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.bt-rollers{
    display: none;
}

.img-conteiner-items{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
.img-item{
    background-color: #000;
    width: 4rem;
    height: 5rem;
}

.product-title{
    height: 3rem;
    width: max-content;
}

.product-info-area{
    margin-left: 3rem;
    height: 100%;
}

.price-rate{
    width: 13rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.price-rate .price{
    font-size: 1.5rem;
    color: var(--theme-color-green);
}

.selection-type-product{
    width: 33rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.select-size,.select-color-area{
    border: none;
    width: 14rem;
    height: 2.5rem;
    align-items: center;
    display: flex;
    flex-direction: row;
    padding: 0rem 1rem 0rem 1rem;
    font-size: 0.9rem;
    gap: 1rem;
}

.size-bt{
    width: 2rem;
    height: 2rem;
    border: none;
}
.size-bt:hover{
    cursor: pointer;
    background-color: var(--theme-color-green);
}

.select-color-area{
    justify-content: space-between;
}
.select-color{
    background: transparent;
    border: none;
    width: 2rem;
    height: 2rem;
}
.select-color:hover, .select-size:hover{
    cursor: pointer;
}

.product-quanties{
    display: flex;
    flex-direction: row;
    width: 10rem;
    height: 2.5rem;
    align-items: center;
    justify-content: space-between;
}

.product-quanties .bt{
   width: 2.5rem;
   height: 2rem;
   display: flex;
   align-items: center;
   text-align: center;
   justify-content: center;
   font-size:1.2rem;
   border: none;
   border-radius: 3px;

}

.product-quanties .bt:hover{
    cursor: pointer;
    background-color: var(--theme-color-green);
}

.bt-area{
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    width: 18rem;
    height: 2.5rem;
    justify-content: space-between;
    align-items: center;
}
.bt-area .bt-product{
    border: none;
    background-color: #fff;
    align-items: center;
    text-align: center;
    width: 2rem;
    height: 2.5rem;
}
.bt-area .bt-product:hover{
    cursor: pointer;
    scale: calc(1.1);
}
.bt-product.add-to-cart{
    width: 10rem;
    margin-top: 1rem;
    background-color: var(--theme-color-green);
    color: #fff;
}
.bt-area-bt{
    border: none;
}
.bt-area-bt:hover{
    cursor: pointer;
    scale: calc(1.2);
}




.description{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: max-content;
    justify-content: space-around;
}


.desc-title-area{
    display: flex;
    flex-direction:row;
    width: 100%;
    height: 2.5rem;
    gap: 1rem;
    text-align: center;
    font-weight: 600;
    align-items: center;
}

.desc-title-area .bt{
    display: flex;
    width: 9rem;
    height: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
    border: none;
}
.desc-title-area .bt:hover{
    cursor: pointer;
    background-color: var(--theme-color-green);
    color: #fff;
}
.desc-area-paragraph{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-top: 2rem;
}