@media screen and (max-width: 500px) {

.best-selers{
    height: 45rem;
    display: flex;
    justify-content: center;
}

.conteiner{
   text-align: center;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
}

.bestSellers-info-area{
    margin-left: 0rem;
}

.bestSellers-title{
    font-size:2rem;
}

.bestSellers-subtitle{
    font-size: 1rem;
    margin-bottom: 1rem;
}

.list{
    gap:0.5rem;
    display: grid;
    grid-template-columns: 2fr;
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas: 
        "card card " 
        "card card"
    ;
}

}