.categorie-card{
    margin-left: -2rem;
    width: 100%;
    height: 25rem;
    text-align: center;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.categorie-card:hover{
    cursor: pointer;
}

.squad-2{
    position: relative;
    border: 1px solid #fff;
    width: 14.2rem;
    height: 14.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(45deg);
}

.squad-1{
    background-color: #202344;
    width: 13rem;
    height: 13rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
}

.categorie-title-area{
    transform: rotate(-45deg);

}

.categorie-title,.winter-colection{
    font-size: 1rem;
    font-weight: 300;
}


@media screen and (max-width:500px) {
    .categorie-card{
        margin-left: 0rem;
        height:25rem;
    }
      
}