@media screen and (max-width:500px) {
    
    .footer-fr1{
      height: max-content;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
    }

    .footer-fr1 span{
        font-size: 1rem;
        width: 90%;
    }

    .footer-fr2{
        flex-direction: column;
        gap: 2rem;
        margin-top: 2rem;
    }

    .nav-contact-footer h2{
        font-size: 1.5rem;
    }
    
    .nav-contact-footer span{
        font-size:1rem;
    }
    
    .footer-fr3{
        height: 8rem;
        flex-direction: column;
        gap: 0.5rem;
        padding-bottom: 1rem;
        box-sizing: border-box;
    }
    .footer-fr3-span{
        margin-top: 1rem;
        text-align: center;
    }
    .footer-fr3-flags{
        width: 70%;
    }
}