.left-area{
    width: 17rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 1rem;
}

.lista-categories{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.list-item-categories{
    list-style-type: square;
    color: #bcbcbc;
}
.list-item-categories span{
    color: #000;
}






.left-area-priceFilter{
    width: 16.5rem;
    height: 10rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.search-filter-area{
    width: 3rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.search-filter-area input{
    width: 5.5rem;
    height: 1.5rem;
    align-items: center;
    text-align: center;
}

.bt-search-price-icon{
    background-color: #00a951;
    border: none;
}

.tt{
    font-weight: bold;
}






.left-area-size{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.bt-size-area{
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 0.1rem;
}
.bt-size-selection{
    width: 3rem;
    height: 1.9rem;
    background-color: #fff;
    border: none;
}

.bt-size-selection:hover{
    cursor: pointer;
    background-color: #00a951;
}





.left-area-colors{
    width: 100%;
    height: 10rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}






.left-area-topRated{
    height: 20rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;

}

.img-icon-topRated{
    background-color: #000;
    width: 5rem;
    height: 6rem;
}



.left-area-popupAd{
    width: 100%;
    height: 10rem;
}
.img-ads-link{
    width: fit-content;
    position: relative;
}
.img-ads{
    width: 16rem;
    height: 16rem;
    opacity: 0.8;
}

.ads-range-square{
    position: absolute;
    top:6.6rem;
    left: -1.8rem;
    background-image: linear-gradient(to right, #f21818 0%, #ff4d00 100%);
    width: 19.5rem;
    height: 3rem;
    transform: rotate(-45deg);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 2rem;
    font-weight: bolder;

}


@media screen and (max-width:500px){
    .left-area{
        display: none;
    }
}

