@import "../../../styles/colors.css";

.review-area{
    margin-top: 2rem;
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    margin-bottom: 3rem;
}


.reviews{
    width: 100%;
    height: max-content;
}

.reviews-title{
    color: var(--theme-color-gray);
}

.review-item {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
    gap: 1rem;
}

.review-item img{
    width: 8rem;
}

.review-item-desc{
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 1rem;
}

.rate-date-conteiner{
    display: flex;
    gap: 1rem;
}





.add-review{
    margin-top: 1rem;
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.add-review-input-area{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}

.add-review-input-area .inp{
    width: 15rem;
    height: 2rem;
    padding-left: 1rem;
    border: 1px solid var(--theme-color-gray);
    border-radius: 3px;
}

.add-rating-review-area{
    display: flex;
    flex-direction: row;
    width: 70%;
    justify-content: space-between;
}


.add-rating-area{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.rating-ul{
    display: flex;
    flex-direction: row;
    list-style: none;
}

.add-review-text{
    height: 10rem;
    border: 1px solid var(--theme-color-gray);
}

.add-review-submit{
    width: 5rem;
    height: 2rem;
    border: 1px solid var(--theme-color-gray);
    border-radius: 3px;
    transition: 0.2s;
}
.add-review-submit:hover{
    cursor: pointer;
    background-color: var(--theme-color-green);
    scale: calc(1.1);
    color: #fff;
}