.clients{
    width: 50%;
    height: 14rem;
    background-color: #fff;
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: space-around;
}

@media screen and (max-width:500px){

    .clients{
        width: 100%;
        height: 10rem;
        margin-top: 0rem;
        margin-bottom: 3rem;
    }
}