
@media screen and (max-width: 500px){

.main-slider{
    height: 25rem;
}

.info-area{
    width: 25rem;
    height: 20rem;
    margin-left: 0rem;

    align-items: center;

    padding-top: 0;
    justify-content: space-around;
}

.title-1, .title-2{
    font-size: 2.6rem;
}

.title-2{
    margin-top: -2.5rem;
}

.subtitle{
    margin-bottom: 0rem;
    width: 20rem;
    text-align: center;
    margin-top: -2rem;
}

.bt-buy{
    width: 10rem;
    height: 3rem;
    margin-top: -2rem;
    font-size: 1.5rem;
}
.bt-buy:hover{
    cursor: pointer;
    color: #00a951;
    border:1px solid #00a951;
    border-left: none;
    transform: scale(1.2);
}

.button-cut-desing1{
   top: -0.5px;
}

.button-cut-desing2{
    top: 36.2px; 
 }

}
