.checkout-area{
    width: 90%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-around;
    margin-bottom: 6rem;
}

.checkout-component{
    align-items: left;
    padding-top: 2rem;
    text-align: left;
    width: 100%;
    height: max-content+3rem;
}

.title-checkout-components{
    height: max-content;
    font-size: 1.2rem;
}

.input-checkout{
    width: 100%;
    height: 2rem;
    font-size: 1rem;
    font-weight: bold;
}

.checkout-button {
   font-size: 1.1rem;
}

.checkout-button.update-totals{
    width: 10rem;
    height: 3rem;
}

.checkout-component-usegift{
  width: 100%;
  text-align: left;
}


.area-input-shipping{
    width: 90%;
    margin-bottom: 2rem;
}

.title-input-checkout:nth-child(n+2){
 margin-top: 1rem;
}


.checkout-component-table{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: max-content;
    position: relative;
}

.card-calculation-table{
    width: 100%;
}

.bt-link{
    width: 100%;
    position: absolute;
    top: 15rem;
}

.checkout-button-calculation-table{
    margin-left:0rem;
    width: 12rem;
    height: 3rem;
}


.empty-Car{
    display:flex;
    flex-direction:column;
    height:10rem;
    margin-top:3rem;
    align-items: center;
    justify-content: center;
