@import "../../../styles/colors.css";

@keyframes slidein {
    from {
      transform: translateX(100%);
    }
  
    to {
      transform: translateX(0%);
    }
  }

.sidebarMenu{
    width: 15rem;
    height: 100vh;
    background-image: linear-gradient(to right, #000 40%,#363636 80%, #4a4a4a 100%);
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1001;
    transition: 0.5 ease;
    color: #fff;
    flex-direction: column;
    animation: 1s slidein
}

.close-sidebar-icon{
    margin-left: 1rem;
    margin-top: 0.5rem;
    border: none;
    background-color: transparent;
    width: fit-content;
}

.close-sidebar-icon:hover{
    cursor: pointer;
}

.navigation-area{
    width: 100%;
    height: 20rem;
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.loginArea{
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.loginArea-hr{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    border: none;
    border-top : 1px solid #4a4a4a
}


.buttons-area-login{
    display: flex;
    flex-direction: row;
    width:9rem;
    justify-content: space-around;
    margin: 0.5rem 0 0.5rem 0;
}

.options-area{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    gap: 3rem;
}

.options-area .bt:hover, .buttons-area-login .bt:hover{
    cursor: pointer;
    color: var(--theme-color-green);
    transform: scale(1.1);
}